@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap);
.card {
  background-color: white;
  text-align: center;
  margin-top: 4em;
  border-radius: 10px;
}

.image__wrapper {
  border: 1px solid black;
  position: absolute;
  width: 75px;
  height: 75px;
  border-radius: 50%;
  background-color: var(--violet);
  margin-top: -2em;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.card__image {
  object-fit: contain;
  padding: 1em;
  -webkit-align-items: center;
          align-items: center;
}

.card__wrapper {
  padding: 3em 1em 3em 1em;
}

.card__line {
  height: 3em;
  width: 0.5em;
  background-color: var(--cyan);
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

p {
  color: var(--grayishViolet);
}

@media (min-width: 905px) {
  .image__wrapper {
    position: relative;
  }

  .card__line {
    display: none;
  }

  .card__wrapper{
      margin-bottom: 4em;
  }

}

main {
    background-color:  hsl(257, 7%, 90%);
    margin-top: -5.6em;
    position: relative;
    z-index: 1;
    width: 100%;
    padding-bottom: 4em;
}

.features__wrapper{
    margin-left: 1em;
    margin-right: 1em;
    
}

.summary{
    padding-top: 10em;
    text-align: center;

}

.summary > h3 {
    font-size: 2em;
}

.summary > p {
    color: var(--grayishViolet);
    font-size: 18px;
}

.cards__container{
    margin-top: 5em;

}


@media (min-width: 905px){
    .cards__container{
        display: -webkit-flex;
        display: flex;
        grid-gap: 2em;
        gap: 2em;
        -webkit-justify-content: center;
                justify-content: center;
    }

    .summary{
        margin-right: 10em;
        margin-left: 10em;
    }

    .features__wrapper{
        margin-left: 9em;
        margin-right: 9em;
    }

}


.link__container{
    background-color: white;
    border-radius: 15px;
    position: relative;
    z-index: 999;
}

.link__wrapper{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}
.link__wrapper > h3 {
    margin: 0;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
}

.divisor{
    border-bottom: 1px solid;
    width: 100%;
    height: 0px;
}

.shortener__wrapper{
    padding: 1em;
}

.copy__button{
  border: none;
  cursor: pointer;
  position:relative;
  background-color: var(--cyan);
  color: white;
  white-space: nowrap;
  border-radius: 10px;
  width: 100%;
  padding: 0.5em;
  margin-top: 1em;
  z-index: 999;
}

.button__clicked{
    border: none;
    cursor: pointer;
    background-color: var(--darkViolet);
    color: white;
    white-space: nowrap;
    border-radius: 10px;
    width: 100%;
    padding: 0.5em;
    margin-top: 1em;


}

.shortener__wrapper > h3 {
    margin: 0;
    color: var(--cyan);
}

@media (min-width: 1250px) {

    .divisor{
        display: none;
    }
    .link__wrapper{
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        padding: 1em;

    }

    .shortener__wrapper{
        display: -webkit-flex;
        display: flex;
        -webkit-align-items: center;
                align-items: center;
        -webkit-justify-content: space-between;
                justify-content: space-between;
    }

    .new__link{
        white-space: nowrap;
    }

    .button__clicked, .copy__button{
        margin-top: 0;
        margin-left: 1em;
    }
}


ul {
  list-style: none;
  padding: 0;
}

footer {
  background-color: var(--darkBlue);
  color: white;
}

.footer__webrepresentation > h3 {
  margin-top: 1em;
  margin-bottom: 0;
  font-size: 2em;
}
.social__icons{
    margin-top: 2em;
    margin-bottom: 4em;
    cursor: pointer;
}

.social__icons > img:hover{
    -webkit-filter: invert(74%) sepia(58%) saturate(469%) hue-rotate(119deg) brightness(94%) contrast(92%);
            filter: invert(74%) sepia(58%) saturate(469%) hue-rotate(119deg) brightness(94%) contrast(92%);
}
.social__icons > img:not(:last-child){
    margin-right: 0.8em;
}


.footer__wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;

}

.footer__links > ul > li:first-child {
  font-weight: 700;
  margin-bottom: 1em;
}

.footer__links > ul > li:not(:first-child) {
  color: var(--grayishViolet);
  cursor: pointer;
}

.footer__links > ul > li:not(:first-child):hover {
    color: var(--cyan);
  }

.footer__links > ul > li:not(:first-child:not(:last-child)) {
  margin-bottom: 0.5em;
}

svg:not(:last-child){
  margin-right: 1em;
}

path:hover{
  fill: cyan;
}
@media (min-width: 905px) {
  .footer__wrapper {
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: baseline;
            align-items: baseline;
    margin-left: 9em;
    margin-right: 9em;
    text-align: left;
    text-align: initial;

  }

  .footer__links{
      display: -webkit-flex;
      display: flex;
      text-align: left;
      -webkit-align-items: baseline;
              align-items: baseline;
      -webkit-flex: 1 1;
              flex: 1 1;
      -webkit-justify-content: space-evenly;
              justify-content: space-evenly;
      width: 50%;
  }

  .social__icons{
      margin-top: 1em;
      margin-bottom: 0;
      margin-left: 1em;
  }


.footer__webrepresentation > h3 {
    margin-right: 5em;
  }
}



header {
  margin-right: 1em;
  margin-left: 1em;
}

button{
    cursor: pointer;
}

