    border:none;
    background-color: inherit;
    color: white;
    font-weight: 600;
    margin-bottom: 1em;
}


.mobile__register{
    border:none;
    background-color: var(--cyan);
    color: white;
    width: 100%;
    white-space: nowrap;
    border-radius: 15px;
