}

.mobile__container{
    padding: 1em;
    background-color: var(--violet);
    border-radius: 10px;
}

.buttons{
    margin-top: 2em;
}


@media (min-width: 905px){
    .burger,.mobile__container{
        display: none;
    }

    .menu__container{
