    }


    .header__container{
        margin-right: 8em;
        margin-left: 8em;
    }

    .mobile__login{
        margin: 0;
        margin-right: 1em;
        color: var(--grayishViolet);
    }

    .mobile__login:hover{
        color: black;
    }

    .mobile__register{
        margin: 0;
        width: 100%;
        padding-right: 1em;
        padding-left: 1em;
        
    }

    .header__buttons{
        display: -webkit-flex;
        display: flex;
    }


